@font-face {
  font-family: "Lato";
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2");
  src: url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2");
  src: url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
  margin: 0;
  position: relative;
}
.wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 320px;
  min-height: 100%;
}
.main-wrapper {
  flex-grow: 1;
}
.main-nav {
  position: relative;
}
.main-nav--js {
  position: absolute;
  z-index: 1;
}
.main-nav__list {
  width: 320px;
  padding: 0;
  margin: 0;
  list-style: none;
  top: 0;
  left: 0;
}
.main-nav__item {
  border-bottom: 1px #e6e6e6 solid;
}
.main-nav__link {
  padding-top: 22px;
  padding-bottom: 24px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
}
.menu-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
  padding-bottom: 12px;
  padding-left: 20px;
  padding-right: 20px;
  border-bottom: 1px #e6e6e6 solid;
}
.menu-button {
  background-color: inherit;
  border: none;
}
.menu-button--close {
  background-image: url("../../img/icons/menu-mobile-close.svg");
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
  width: 20px;
  height: 20px;
}
.menu-button--open {
  background-image: url("../../img/icons/menu-mobile-open.svg");
  background-size: 24px 18px;
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 24px;
}
.select-program {
  padding-top: 27px;
  box-sizing: border-box;
  width: 320px;
  min-height: 302px;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/mobile/index-background-mobile.jpg");
  background-position: top, top;
  background-repeat: no-repeat;
  background-size: 320px 302px, 320px 302px;
}
.select-program__title {
  width: 260px;
  margin: 0 auto;
  margin-bottom: 25px;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  color: #ffffff;
  text-align: center;
  overflow-wrap: anywhere;
}
.select-program__slogan {
  width: 198px;
  margin: 0 auto;
  margin-bottom: 29px;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  color: #ffffff;
  text-align: center;
  overflow-wrap: anywhere;
  text-transform: uppercase;
}
.select-program__button {
  margin: 0 auto;
}
.select-program__image {
  display: flex;
}
.button {
  display: block;
  box-sizing: border-box;
  padding: 7px 44px;
  background-color: #68b738;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
}
.button--mobile-wide {
  width: 280px;
}
.button--show-more {
  background-color: #f2f2f2;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
}
.diet-type {
  padding-bottom: 20px;
  padding-top: 20px;
}
.diet-type__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.diet-type__item {
