  padding-right: 42px;
  background-color: inherit;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: #000000;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
}
.work-principle {
  padding-bottom: 53px;
}
.work-principle__title {
  margin: 0;
  margin-bottom: 40px;
}
.work-principle__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.work-principle__item:not(:last-child) {
  margin-bottom: 26px;
}
.work-principle__description {
  width: 280px;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  padding-left: 80px;
  text-align: left;
}
.section-title {
  padding: 0;
  width: 280px;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  color: #000000;
  text-align: left;
}
.result {
