}
.result__expenses {
  width: 280px;
  padding: 0 10px;
  margin: 0;
  margin-bottom: 20px;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #000000;
  text-align: center;
}
.contact {
  padding: 26px 0;
}
.contact--catalog {
  background-color: #f2f2f2;
}
.contact__container {
  display: flex;
  width: 280px;
  margin: 0 auto;
}
.contact__partnership {
  width: 139px;
