  text-align: left;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #444444;
}
.catalog {
  width: 320px;
  padding-top: 27px;
}
.catalog__title {
  padding-bottom: 41px;
  border-bottom: 1px #d9d9d9 solid;
}
.catalog__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.catalog__card {
  padding-top: 19px;
  padding-bottom: 25px;
  border-bottom: 1px #d9d9d9 solid;
}
.catalog__card--show-more {
  padding-top: 25px;
}
.catalog__card-container-show-more {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.catalog__card-img--show-more {
