  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #222222;
  text-align: left;
}
.catalog__card-title--show-more {
  text-align: center;
  width: 220px;
  margin-bottom: 8px;
}
.catalog__card-term,
.catalog__card-desc {
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  color: #444444;
  text-align: left;
}
.catalog__card-term--show-more,
.catalog__card-desc--show-more {
  text-align: center;
  width: 220px;
  margin-bottom: 21px;
}
.catalog-optional {
  padding-top: 25px;
  padding-bottom: 35px;
}
.catalog-optional__title {
  width: 280px;
  padding: 0;
  padding-bottom: 36px;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  color: #000000;
  text-align: left;
  border-bottom: 1px #d9d9d9 solid;
}
.catalog-optional__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.catalog-optional__card {
  padding-top: 13px;
  padding-bottom: 19px;
  border-bottom: 1px #d9d9d9 solid;
}
.catalog-optional__card-title {
  width: 280px;
  padding: 0;
  padding-bottom: 13px;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: #222222;
