}
.catalog-optional__bonus {
  padding-top: 35px;
}
.catalog-optional__bonus-container {
  width: 280px;
  min-height: 290px;
  box-sizing: border-box;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/mobile/cat-in-bag-mobile.jpg");
  background-color: rgba(104, 183, 56, 0.85);
  background-position: top, top;
  background-repeat: no-repeat;
  background-size: 280px 290px, 280px 290px;
  padding-top: 189px;
  padding-bottom: 10px;
  padding-left: 58px;
  padding-right: 61px;
}
.catalog-optional__bonus-text {
